<template>
    <a v-bind:href="url"><button :class="btnClass" v-bind:style="style">
        <i v-bind:class="icon"></i> {{ label }}
    </button></a>
</template>

<script>
import ENUMS from '@/enum.js'

export default {
  name: 'SocialLink',
  props: {
      mode: String,
      info: {
        url: String,
        color: String,
        label: String,
        icon: String,
      }
  },
  data() {
    return {
        btnClass: 'btn',
        url: this.info.url,
        style: {
            backgroundColor: this.info.color
        },
        label: this.info.label,
        icon: this.info.icon.split(" "),
    }
  },
  mounted() {
      this.btnClass = this.mode==ENUMS.dark? "btn dark-btn":"btn light-btn"
  }
};  
</script>

<style scoped>
    .btn {
        background-color: grey;
        color: white;
        font-weight: 600;
        letter-spacing: 0.1em;
        font-size: 0.8em;
        width: 95%;
        display: block;
        padding: 1.2em;
        border: none;
        margin: 1em auto;
        border-radius:0;
    }

    a {
        text-decoration: none!important;
    }

    .dark-btn:hover {
        background-color: transparent!important;
        color: white;
        border:  2px solid white;
    }

    .light-btn:hover {
        background-color: transparent!important;
        color: #0A0A23;
        border:  2px solid #0A0A23;
    }
</style>