<template>
  <div class="main">
    <img
      v-bind:src="require(`@/assets/${this.imgURL}`)"
      v-bind:style="styleObject"
      />
    <h4 v-bind:style="this.headerColor">{{ this.handle }}</h4>
  </div>
</template>

<script>
import ENUMS from '@/enum.js'

export default {
  name: 'Header',
  props: {
    imgURL: String,
    mode: String,
    handle: String
  },
  data() {
    return {
      styleObject: {
        borderRadius:"50%",
        maxWidth: "100px",
        textAlign: "center",
        objectFit: "cover",
      },
    }
  },
  computed: {
    headerColor() {
      return {
        color: this.mode==ENUMS.dark? "#ffffff": "#0A0A23"
      }
    }
  }
}
</script>

<style scoped>

  h4 {
    margin-top: 0.5em;
  }
</style>
